/* eslint-disable import/no-extraneous-dependencies */
/* eslint-disable jsx-a11y/label-has-for */
/* eslint-disable object-curly-newline */
/* eslint-disable react/jsx-one-expression-per-line */
/* eslint-disable linebreak-style */
import React, { useEffect } from 'react'
import { Link } from 'react-router-dom'
import { Form, FormGroup, Label, Input, Button } from 'reactstrap'
import CustomScroll from 'react-custom-scroll'

import forgotImage from '../../../assets/images/forgot-left-img.png'


function ForgotPassword(props) {
  useEffect(() => {
    document.body.classList.add('overflow-hidden')

  }, [])
  return (
    <div>
      <div className="login-sec">
        <div className="d-flex full-height">
          <div className="login-left justify-center">
            <div className="align-center justify-center flex-column">
              <img src={forgotImage} alt="forgot-img" />
              <h3 className="text-center">Don’t worry we can help</h3>
            </div>
          </div>
          <div className="login-right">
            <p>Don’t have an account? <a href=" # ">Create Account</a></p>
            <div className="login-blk justify-center flex-column">
              <CustomScroll>
                <div className="panel-content-custom">
                  <h2>Forgot Password</h2>
                  <h6 className="gray-color">Design regular t-shirts and full sleeve t-shirts for men, women and kids in 100% Cotton and 100% polyester fabrics, customised with your logo, photo, message or text.</h6>
                  <Form>
                    <FormGroup>
                      <Label>Email ID / Phone Number</Label>
                      <Input type="text" name="email" />
                    </FormGroup>
                    <Button className="login-btn m-15">Submit</Button>
                  </Form>
                  <div className="back-to-login">
                    <a href=" # ">Back to Login</a>
                  </div>
                </div>
              </CustomScroll>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
export default ForgotPassword
