import React from 'react'
import { Button,NavLink } from 'reactstrap'
import prodImage1 from '../assets/images/t-shirt-1.png'
// import prodArrow from '../assets/images/product-hover-arrow.svg'
import wishlistIcon from '../assets/images/wishlist-ic.svg'

export default function Productcard(props) {
  {/* Match Url */ }
  const { url } = props.match
  const show = [`${process.env.PUBLIC_URL}/b2b-my-wishlist`,`${process.env.PUBLIC_URL}/my-wishlist`].indexOf(url) > -1;
  const showBusiness = url.includes(`${process.env.PUBLIC_URL}/b2b-`);
  return (
    <div>
      <div className="products-blk">
        <div className="product-img">
          <NavLink href={showBusiness ? `${process.env.PUBLIC_URL}/b2b-product-details` : `${process.env.PUBLIC_URL}/product-details`}><img src={prodImage1} alt="prodImage1" className="tshirt-img" /></NavLink>
          {/* <a href=" # " className="product-hover-arrow"><img src={prodArrow} alt="prodarrow" /></a> */}
          <Button className="add-wishlist">
            <img src={wishlistIcon} alt="wishlist-ic" />
          </Button>
        </div>
        <div className="product-info">
          <div className="text-center">
          <NavLink href={showBusiness ? `${process.env.PUBLIC_URL}/b2b-artist-details` : `${process.env.PUBLIC_URL}/artist-details`} className="artist-nm">Designed by Anonymous</NavLink>
            <h6><NavLink href={showBusiness ? `${process.env.PUBLIC_URL}/b2b-product-details` : `${process.env.PUBLIC_URL}/product-details`}>Heart Print Pastel Coral Slub Pimk Polo T-shirt</NavLink></h6>
          </div>
          <div className="product-price-color">
            <div className="justify-space-between">
              <div className="align-center grow-1">
                <div className="price">₹349</div>
                <div className="old-price">₹699</div>
                <div className="product-discount">15% off <span>Applied at checkout</span></div>
              </div>
              <div className="product-colors-blk align-center shrink-0">
                <div className="more-color-sign">+</div>
                <ul>
                  <li><div className="product-colors colo-blue" style={{ backgroundColor: '#009CD7' }}> </div></li>
                  <li><div className="product-colors colo-pink" style={{ backgroundColor: '#C112EB' }}> </div></li>
                  <li><div className="product-colors colo-yellow" style={{ backgroundColor: '#EBD312' }}> </div></li>
                  <li><div className="product-colors colo-red" style={{ backgroundColor: '#E3015B' }}> </div></li>
                </ul>
              </div>
            </div>
          </div>
          { show ? 
            <div className="text-center">
              <Button className="btn-outline color-blue add-cart">ADD TO CART</Button>
            </div>
            :
            <div></div>
          }
        </div>
      </div>
    </div>
  )
}
