import React from 'react'
import { Container, Row, Col, NavLink } from 'reactstrap'
import Productcard from '../../../components/ProductCard'

export default function TrendingProducts(props) {
  {/* Match Url */ }
  const { url } = props.match
  const showBusiness = url.includes(`${process.env.PUBLIC_URL}/b2b-`);
  return (
    <div>
      <div className="section-padding trending-products-sec">
        <Container>
          <div>
            <h1 className="section-title">Trending Products</h1>
            <Row>
              <Col xl="3" lg="4" md="6">
                <Productcard {...props}> </Productcard>
              </Col>
              <Col xl="3" lg="4" md="6">
                <Productcard {...props}> </Productcard>
              </Col>
              <Col xl="3" lg="4" md="6">
                <Productcard {...props}> </Productcard>
              </Col>
              <Col xl="3" lg="4" md="6">
                <Productcard {...props}> </Productcard>
              </Col>
            </Row>
            <div className="view-all-btn">
              <NavLink href={showBusiness ? `${process.env.PUBLIC_URL}/b2b-products` : `${process.env.PUBLIC_URL}/products`}  className="btn-1">View All</NavLink>
            </div>
          </div>
        </Container>
      </div>
    </div>
  )
}
