/* eslint-disable jsx-a11y/label-has-associated-control */
/* eslint-disable import/no-extraneous-dependencies */
/* eslint-disable jsx-a11y/label-has-for */
/* eslint-disable object-curly-newline */
/* eslint-disable react/jsx-one-expression-per-line */
/* eslint-disable linebreak-style */
import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { Form, FormGroup, Label, Input, Button, InputGroup, InputGroupAddon, InputGroupText, Row, Col } from 'reactstrap'
import classnames from 'classnames';
import CustomScroll from 'react-custom-scroll'
import PhoneInput from 'react-phone-input-2'
import profileSetupImage from '../../../assets/images/profile-setup-image.png'
import facebookIcon from '../../../assets/images/facebook-icon.svg'
import instagramIcon from '../../../assets/images/instagram-icon.svg'
import twitterIcon from '../../../assets/images/twitter-icon.svg'
import Select from 'react-select'

function ProfileSetupForm(props) {
  const cityOptions = [
    { value: 'Ahmedabad', label: 'Ahmedabad' },
    { value: 'Saskatoon', label: 'Saskatoon' }
  ]
  const stateOptions = [
    { value: 'Gujarat', label: 'Gujarat' },
    { value: 'Alberta', label: 'Alberta' }
  ]
  const countryOptions = [
    { value: 'India', label: 'India' },
    { value: 'Canada', label: 'Canada' }
  ]
  useEffect(() => {
    document.body.classList.add('overflow-hidden')

  }, [])
  const [showNext, setshowNext] = React.useState(false)
  const showNextStep = () => setshowNext(true)
  const showPreviousStep = () => setshowNext(false)
  return (
    <div>
      <div className="login-sec signup-sec profile-setup">
        <div className="d-flex full-height">
          <div className="login-left justify-center">
            <div className="align-center justify-center flex-column">
              <img src={profileSetupImage} alt="profileSetup-img" />
              <h3 className="text-center">Style is a way to say who you are without  <br /> having to speak.</h3>
            </div>
          </div>
          <div className="login-right">
            <p><a href=" # ">Skip</a></p>
            <div className="login-blk justify-center flex-column">
             <CustomScroll>
                <div className="panel-content-custom">
                  <h2>Profile Set Up</h2>
                   <Form>
                     { showNext ? null :
                        <div className="initial-step steps">
                          <FormGroup>
                            <Label>Company Name</Label>
                            <Input type="text" name="company-name" />
                          </FormGroup>
                          <FormGroup>
                            <Label>Contact Person</Label>
                            <Input type="text" name="contact-person" />
                          </FormGroup>
                          <FormGroup>
                            <Label>Pan No</Label>
                            <Input type="text" name="pan-no" />
                          </FormGroup>
                          <FormGroup>
                            <Label>GST No</Label>
                            <Input type="text" name="pan-no" />
                          </FormGroup>
                          <FormGroup>
                            <Label>Facebook Link</Label>
                            <InputGroup>
                              <InputGroupAddon addonType="prepend">
                                <InputGroupText><img src={facebookIcon} alt="facebook"/></InputGroupText>
                              </InputGroupAddon>
                              <Input type="text" name="facebook-link" />
                            </InputGroup>
                          </FormGroup>
                          <FormGroup>
                            <Label>Instagram Link</Label>
                            <InputGroup>
                              <InputGroupAddon addonType="prepend">
                                <InputGroupText><img src={instagramIcon} alt="instagram"/></InputGroupText>
                              </InputGroupAddon>
                              <Input type="text" name="instagram-link" />
                            </InputGroup>
                          </FormGroup>
                          <FormGroup>
                            <Label>Twitter Link</Label>
                            <InputGroup>
                              <InputGroupAddon addonType="prepend">
                                <InputGroupText><img src={twitterIcon} alt="twitter"/></InputGroupText>
                              </InputGroupAddon>
                              <Input type="text" name="twitter-link" />
                            </InputGroup>
                          </FormGroup>
                          <Button className="login-btn" onClick={showNextStep}>Next</Button>
                        </div>
                      }
                      { showNext ?
                        <div className="last-step steps">
                          <h6>Shipping Address</h6>
                          <FormGroup>
                            <Label>First Name</Label>
                            <Input type="text" name="first-name" />
                          </FormGroup>
                          <FormGroup>
                            <Label>Phone Number</Label>
                            <div className="country-code-list">
                              <PhoneInput
                                country={'in'}
                                // value={this.state.phone}
                                // onChange={phone => this.setState({ phone })}
                              />
                            </div>
                          </FormGroup>
                          <FormGroup>
                            <Label>Email Address</Label>
                            <Input type="email" name="email" />
                          </FormGroup>
                          <FormGroup>
                            <Label>Address</Label>
                            <Input type="text" name="address" />
                          </FormGroup>
                          <Row>
                            <Col md="6">
                              <FormGroup>
                                <Label>Zip Code</Label>
                                <Input type="text" name="zip-code" />
                              </FormGroup>
                            </Col>
                            <Col md="6">
                              <FormGroup>
                                <Label>City</Label>
                                <div className="normal-dropdown">
                                  <Select
                                    options={cityOptions}
                                    menuPlacement = "auto"
                                    placeholder={<span></span>}
                                  />
                                </div>
                              </FormGroup>
                            </Col>
                          </Row>
                          <FormGroup>
                            <Label>State</Label>
                            <div className="normal-dropdown">
                              <Select
                                options={stateOptions}
                                menuPlacement = "auto"
                                placeholder={<span></span>}
                              />
                            </div>
                          </FormGroup>
                          <FormGroup>
                            <Label>Country</Label>
                            <div className="normal-dropdown">
                              <Select
                                options={countryOptions}
                                menuPlacement = "auto"
                                placeholder={<span></span>}
                              />
                            </div>
                          </FormGroup>
                          <Row>
                            <Col md="6">
                              <Button className="btn-outline" onClick={showPreviousStep}>Previous</Button>
                            </Col>
                            <Col md="6">
                              <Button className="login-btn" type="submit">Submit</Button>
                            </Col>
                          </Row>
                        </div>
                        :
                        null
                      }
                    </Form>
                </div>
              </CustomScroll>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
export default ProfileSetupForm