/* eslint-disable import/no-extraneous-dependencies */
/* eslint-disable jsx-a11y/label-has-for */
/* eslint-disable object-curly-newline */
/* eslint-disable react/jsx-one-expression-per-line */
/* eslint-disable linebreak-style */
import React, { useEffect } from 'react'
import { Link } from 'react-router-dom'
import { Form, FormGroup, Label, Input, Button } from 'reactstrap'
import CustomScroll from 'react-custom-scroll'
import otpImage from '../../../assets/images/login-left-img.png'
import editic from '../../../assets/images/number-edit-ic.svg'

function EnterOtpForm(props) {
  useEffect(() => {
    document.body.classList.add('overflow-hidden');

  }, [])
  return (
    <div>
      <div className="login-sec">
        <div className="d-flex full-height">
          <div className="login-left justify-center">
            <div className="align-center justify-center flex-column">
              <img src={otpImage} alt="otp-img" />
              <h3 className="text-center">Style is a way to say who you are without<br /> having to speak.</h3>
            </div>
          </div>
          <div className="login-right">
            <div className="login-blk enter-otp-blk justify-center flex-column">
              <CustomScroll>
                <div className="panel-content-custom">
                  <h4>Please enter OTP to verify your account</h4>
                  <h6 className="light-black">OTP sent to <span>+91 123 456 7891</span> <a href=" # " className="edit-number"><img src={editic} alt="edit-ic" /></a></h6>
                  <Form>
                    <FormGroup>
                      <Input type="text" name="email" className="text-center" placeholder="0-0-0-0" />
                    </FormGroup>
                    <div className="otp-time">
                      <span>01:59</span>
                    </div>
                    <Button className="login-btn m-15">Verify</Button>
                    <div className="resend-code">
                      <p>Didn’t Receive Code? <Button className="btn-text">Resend OTP</Button></p>
                    </div>
                  </Form>
                </div>
              </CustomScroll>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
export default EnterOtpForm
