/* eslint-disable import/no-extraneous-dependencies */
/* eslint-disable jsx-a11y/label-has-for */
/* eslint-disable object-curly-newline */
/* eslint-disable react/jsx-one-expression-per-line */
/* eslint-disable linebreak-style */
import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { Form, FormGroup, Label, Input, TabContent, TabPane, Nav, NavItem, NavLink, Button } from 'reactstrap'
import classnames from 'classnames'
import ReCAPTCHA from 'react-google-recaptcha'
import CustomScroll from 'react-custom-scroll'
import PhoneInput from 'react-phone-input-2'
import loginImage from '../../../assets/images/login-left-img.png'
import fbIcon from '../../../assets/images/fb-ic.svg'
import googleIcon from '../../../assets/images/google-ic.svg'

function LoginForm(props) {
  const [activeTab, setActiveTab] = useState('1')
  const toggle = tab => {
    if (activeTab !== tab) setActiveTab(tab)
  }
  const recaptchaRef = React.createRef()
  const TEST_SITE_KEY = 'Site Key'
  const options = [
    { value: '+91', label: '+91' },
    { value: '+92', label: '+92' },
    { value: '+93', label: '+93' },
    { value: '+94', label: '+94' }
  ]
  useEffect(() => {
    document.body.classList.add('overflow-hidden');

  }, [])
  {/* Match Url */ }
  const { url } = props.match
  const showLogin = [`${process.env.PUBLIC_URL}/b2b-login`].indexOf(url) > -1;
  return (
    <div>
      <div className="login-sec">
        <div className="d-flex full-height">
          <div className="login-left justify-center">
            <div className="align-center justify-center flex-column">
              <img src={loginImage} alt="login-img" />
              <h3 className="text-center">Style is a way to say who you are without<br /> having to speak.</h3>
            </div>
          </div>
          <div className="login-right">
            <p>Don’t have an account? <NavLink href={showLogin ? `${process.env.PUBLIC_URL}/b2b-sign-up` : `${process.env.PUBLIC_URL}/sign-up` }>Create Account</NavLink></p>
            <div className="login-blk justify-center flex-column">
              <CustomScroll>
                <div className="panel-content-custom">
                  <Nav tabs>
                    <NavItem>
                      <NavLink
                        className={classnames({ active: activeTab === '1' })}
                        onClick={() => { toggle('1') }}
                      >
                        Log In With Email
                      </NavLink>
                    </NavItem>
                    <NavItem>
                      <NavLink
                        className={classnames({ active: activeTab === '2' })}
                        onClick={() => { toggle('2') }}
                      >
                        Log In With OTP
                      </NavLink>
                    </NavItem>
                  </Nav>
                  <Form>
                    <TabContent activeTab={activeTab}>
                      <TabPane tabId="1">
                          <div className="social-login">
                            <div className="d-flex align-items-center mb-4">
                              <h6>Login With:</h6>
                              <div className="social-ic">
                                <a href=" # "> <img src={googleIcon} alt="google-ic" />Google</a>
                                <a href=" # "> <img src={fbIcon} alt="fb-ic" />Facebook</a>
                              </div>
                            </div>
                            <div className="title-seprator">
                              <h6>OR</h6>
                            </div>
                          </div>
                        <FormGroup>
                          <Label>Email</Label>
                          <Input type="email" name="email" />
                        </FormGroup>
                        <FormGroup>
                          <Label>Password</Label>
                          <Input type="password" name="password" />
                        </FormGroup>
                        <div className="justify-space-between">
                          <div className="checkbox-style">
                            <input type="checkbox" id="test1" />
                            <label for="test1">Remember Me</label>
                          </div>
                          <NavLink href={showLogin ? `${process.env.PUBLIC_URL}/b2b-forgot` : `${process.env.PUBLIC_URL}/forgot`} className="forgot-link">Forgot Password?</NavLink>
                        </div>
                        <div className="reacaptcha">
                          <ReCAPTCHA
                            style={{ display: 'inline-block' }}
                            ref={recaptchaRef}
                            sitekey={TEST_SITE_KEY}
                          />
                        </div>
                        <Button className="login-btn">Login</Button>
                      </TabPane>
                      <TabPane tabId="2">
                        <FormGroup>
                          <Label>Phone Number</Label>
                          <div className="country-code-list">
                            <PhoneInput
                              country={'in'}
                            // value={this.state.phone}
                            // onChange={phone => this.setState({ phone })}
                            />
                          </div>
                        </FormGroup>
                        <div className="reacaptcha">
                          <ReCAPTCHA
                            style={{ display: 'inline-block' }}
                            ref={recaptchaRef}
                            sitekey={TEST_SITE_KEY}
                          />
                        </div>
                        <Button className="login-btn">Get OTP</Button>
                      </TabPane>
                    </TabContent>
                  </Form>
                </div>
              </CustomScroll>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
export default LoginForm
