/* Static Pages */
import AboutUsContent from '../views/About Us/index'
import TermsAndConditionsContent from '../views/TermsAndConditions/index'
import PrivacyPolicyContent from '../views/PrivacyPolicy/index'
import FaqsContent from '../views/FAQs/index'
import BlogsContent from '../views/Blogs/index'
import BlogDetailsContent from '../views/Blogs/BlogDetails/index'
import ContactUsContent from '../views/ContactUs/index'
import ShipmentAndPaymentsContent from '../views/ShipmentAndPayments/index'
import ReturnPolicyContent from '../views/ReturnPolicy/index'

/* Artist Panel */
import ArtistProfileContent from '../views/Artist Panel/ArtistProfile/index'
import DashboardContent from '../views/Artist Panel/Dashboard/index'
import NewProjectsContent from '../views/Artist Panel/NewProjects/index'
