import React from 'react'
import { Container, Button, Row, Col, Form, FormGroup, Label } from 'reactstrap'
import CartList from './components/cart-list'
import PriceSummery from './components/price-summery'
import Productcard from '../../components/ProductCard'
import secureIcon from '../../assets/images/secure-ic.svg'
import Select from 'react-select'

export default function MycartContent(props) {
  {/* Match Url */ }
  const { url } = props.match
  const show = [`${process.env.PUBLIC_URL}/b2b-my-cart`].indexOf(url) > -1;
  const subUser = [`${process.env.PUBLIC_URL}/b2b-sub-user-checkout`].indexOf(url) > -1;
  const users = [
    { value: 'John Smith', label: 'John Smith' },
    { value: 'Mitchell McCay', label: 'Mitchell McCay' }
  ]
  return (
    <div>
      <div className="mycart-sec m-t-100">
        <Container>
          <h2>{subUser ? "My Account" : "My Cart (4)"}</h2>
          {subUser ?
            <div className="subUser-intro">
              <h3>Sub-User’s Cart Check out</h3>
              <Row>
                <Col md="12" lg="8">
                  <Row>
                    <Col md="6">
                      <h5>Select SubUser for check out</h5>
                      <Form>
                        <FormGroup>
                          <div className="normal-dropdown">
                            <Select
                              options={users}
                              menuPlacement = "auto"
                              placeholder={<span></span>}
                            />
                          </div>
                        </FormGroup>
                      </Form>
                    </Col>
                    <Col md={{ size: 3, offset:3 }} className="align-self-center text-right">
                        <a href="#" className="remove color-red">Remove all</a>
                    </Col>
                  </Row>
                </Col>
              </Row>
            </div>
            :
            null
          }
          <Form>
            <Row>
              <Col lg="8">
                <div>
                  <CartList {...props}></CartList>
                  <CartList {...props}></CartList>
                  <CartList {...props}></CartList>
                  {subUser ? 
                    null 
                    :
                    <div className="secure-note">
                      <h5> <img src={secureIcon} alt="secure-ic" /> Safe and Secure Payments - Easy returns - 100% Authentic products.</h5>
                    </div>  
                  }
                </div>
              </Col>
              <Col lg="4">
                <div>
                  <PriceSummery {...props}></PriceSummery>
                </div>
              </Col>
            </Row>
          </Form>
        </Container>
      </div>
      {show || subUser ? 
        null
      :
      <div className="section-padding">
        <Container>
          <h1 className="section-title">Related Products</h1>
          <Row>
            <Col xl="3" lg="4" md="6">
              <Productcard {...props}> </Productcard>
            </Col>
            <Col xl="3" lg="4" md="6">
              <Productcard {...props}> </Productcard>
            </Col>
            <Col xl="3" lg="4" md="6">
              <Productcard {...props}> </Productcard>
            </Col>
            <Col xl="3" lg="4" md="6">
              <Productcard {...props}> </Productcard>
            </Col>
          </Row>
          <div className="view-all-btn">
            <Button href=" # " className="btn-1">View All</Button>
          </div>
        </Container>
      </div>
      }
    </div>
  )
}
