import React from 'react'
import SignUpForm from '../../Auth/SignUp/SignUp'
import NavBar from '../../../components/NavBar'
function SignUp(props) {
  return (
    <div className="auth">
      <NavBar {...props} />
      <SignUpForm {...props} />
    </div>
  )
}
export default SignUp
